import { useTranslation } from "react-i18next";
import { useNavigate, useLocation } from "react-router-dom";
import { ArrowLeft, Sparkles, ChefHat, HelpCircle } from "lucide-react";
import { useLoginRequired } from "@/hooks/useLoginRequired";

const AIRecipeSelect = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const { requireLogin } = useLoginRequired();

  // 感知来源：若从社区进来，返回社区；否则返回首页
  const fromPath = (location.state as any)?.from || "/";

  return (
    <div className="min-h-screen flex flex-col bg-background">
      {/* Header */}
      <div className="px-4 py-3 border-b bg-background shrink-0 sticky top-14 z-10">
        <div className="flex items-center justify-between">
          <button
            onClick={() => navigate(fromPath, { replace: true })}
            className="p-1 -ml-1 hover:bg-muted rounded-md"
          >
            <ArrowLeft className="h-5 w-5" />
          </button>
          <div className="flex items-center gap-2">
            <Sparkles className="h-4 w-4 text-primary" />
            <h2 className="font-medium">{t("aiRecipe.title")}</h2>
          </div>
          <div className="w-6" />
        </div>
      </div>

      {/* Selection Cards */}
      <div className="flex-1 flex flex-col items-center justify-center p-6 gap-6 max-w-md mx-auto w-full">
        <div className="text-center space-y-2 mb-4">
          <h1 className="text-2xl font-bold">{t("aiRecipe.selectTitle")}</h1>
          <p className="text-sm text-muted-foreground">
            {t("aiRecipe.selectDesc")}
          </p>
        </div>

        <div className="grid gap-4 w-full">
          {/* 吃什么 */}
          <button
            onClick={() => navigate("/ai-recipe/what-to-eat", { state: { from: fromPath } })}
            className="flex items-start gap-4 p-5 rounded-2xl border-2 border-border hover:border-primary hover:bg-primary/5 transition-all duration-200 group text-left"
          >
            <div className="w-14 h-14 rounded-xl bg-primary/10 flex items-center justify-center shrink-0 group-hover:scale-105 transition-transform">
              <HelpCircle className="h-7 w-7 text-primary" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold text-base">
                {t("aiRecipe.whatToEat")}
              </h3>
              <p className="text-xs text-muted-foreground leading-relaxed">
                {t("aiRecipe.whatToEatDesc")}
              </p>
            </div>
          </button>

          {/* 做什么 */}
          <button
            onClick={() => { if (requireLogin()) navigate("/ai-recipe/how-to-cook", { state: { from: fromPath } }); }}
            className="flex items-start gap-4 p-5 rounded-2xl border-2 border-border hover:border-primary hover:bg-primary/5 transition-all duration-200 group text-left"
          >
            <div className="w-14 h-14 rounded-xl bg-accent flex items-center justify-center shrink-0 group-hover:scale-105 transition-transform">
              <ChefHat className="h-7 w-7 text-accent-foreground" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold text-base">
                {t("aiRecipe.howToCook")}
              </h3>
              <p className="text-xs text-muted-foreground leading-relaxed">
                {t("aiRecipe.howToCookDesc")}
              </p>
            </div>
          </button>
        </div>
      </div>
    </div>
  );
};

export default AIRecipeSelect;
